/* src/components/ProductCard.css */

.card {
    border: 1px solid #ccc;
    border-radius: 8px;
    width: 300px;
    margin: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    background-color: #fff;
  }
  
  .card-img {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  
  .card-body {
    padding: 16px;
  }
  
  .card-title {
    font-size: 1.5rem;
    color: #003366;
  }
  
  .card-text {
    margin: 8px 0;
    color: #333;
  }
  